.navbar {
    background-color: rgb(34, 40, 39);
    color: hwb(270 84% 14%);
    padding: 10px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .logo {
    font-size: 1.5rem;
  }

  .actions button {
    background-color: hsl(0, 31%, 93%);
    color: hwb(160 9% 90%);
    
    border: none;
    padding: 8px 16px;
    margin-left: 10px;
    cursor: pointer;
  }
  
  .actions button:hover {
    background-color: hsl(249, 86%, 43%);
  }
  
  .actions div {
    display: flex;
    align-items: center;
  }
  
  .actions div button {
    margin-left: 10px;
  }